.transaction {
  .transactionItem {
    padding: 10px;
    width: 100%;
    border-radius: 5px;
    background: #293C8B;
    color: #fff;
    display: inline-block;
    font-family: 'Heebo', sans-serif;
    font-weight: 400;

    .rowItem {
      &:first-child {
        padding-bottom: 20px;
      }

      & > div {
        width: 50%;
      }
    }

    .shortName, .count {
      font-size: 12px;
      margin-right: 5px;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }

    .time, .date, .address {
      font-size: 12px;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }

    .date {
      padding-left: 5px;
    }
  }

  .bigTransactions {
    width: 100%;
    padding: 20px 20px 20px 20px;

    .iconInBtn {
      padding-left: 15px;
    }

    .count {
      font-size: 20px;
      font-weight: bold;
      font-family: 'Heebo', sans-serif;
    }

    .shortName {
      font-size: 20px;
      text-transform: uppercase;
      padding-left: 10px;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }

    .completed {
      background: #B158E2;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
      color: #fff;

      & > span {
        font-size: 12px;
        text-transform: capitalize;
      }
    }

    .pending {
      background: #F3F4F5;
      font-size: 12px;
      color: #65656A;
      width: 120px;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;

      & > span {
        font-size: 12px;
        text-transform: capitalize;
      }
    }
  }
}

