
.header {

  .mobMenu {
    background-color: #000016;
    padding: 25px 40px 0 40px;
    display: none;
  }

  .activeDrawer {
    display: block;
  }

  .drawer.activeDrawer > div:last-child {
    right: 0;
    left: auto;

    ul > li:first-child {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      padding-bottom: 40px;
    }

    ul > li:last-child {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      padding-top: 40px;
    }
  }

  .iconMenu {
    color: #fff;
    font-size: 24px;
    cursor: pointer;
  }


  .headerContent {
    padding: 35px 40px 25px 115px;
    background-color: #000016;

    .mainTitle {
      font-size: 24px;
      color: #fff;
      margin: 0;
      font-family: Heebo, sans-serif;
      font-weight: 100;
    }
  }
}

@media screen and(max-width: 1023px) {
  .header {

    .mobMenu {
      display: flex;
    }

    .mobMenu, .headerContent {
      padding: 20px;
    }
  }
}



