.ReadMore {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 285px;
  margin: 0 20px;

  .ReadMoreMedia {
    height: 100%;
  }

  .ReadMoreTypography {
    margin-left: 5%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    font-style: normal;
    color: #FFFFFF;

    .ReadMoreDate {
      font-weight: bold;
      font-size: 12px;
      line-height: 31px;
      color: inherit;
      font-family: Heebo, sans-serif;
      font-weight: bold;
    }

    .ReadMoreTittle {
      width: 70%;
      font-weight: normal;
      font-style: normal;
      font-size: 20px;
      line-height: 155%;
      color: #FFFFFF;
      font-family: Heebo, sans-serif;
      font-weight: 100;
    }

    .ReadMoreButton {
      margin-left: 0;
      width: 20%;
      font-size: 12px;
      line-height: 18px;
      text-align: center;
      border: 2px solid #B158E2;
      box-sizing: border-box;
      border-radius: 14px;
      color: #FFFFFF;

      & > span {
        font-family: Heebo, sans-serif;
        font-weight: 400;
        text-transform: initial;
      }
    }

  }

}
