.overview {

  .title {
    font-size: 16px;
    color: #fff;
    font-family: 'Heebo', sans-serif;
    font-weight: 400;
  }

  .transactions{
    padding-left: 30px;
  }

  .transactions, .status {
    width: auto;

    .count {
      color: #fff;
      font-size: 30px;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }

    .countOf {
      font-size: 12px;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }
  }



  .status {
    width: 50%;
    .blockWrapper {
      background-color: #293C8B;
      width: 70%;
      padding: 15px 10px;
      border-radius: 5px;
    }
  }

  .wrapper {
    width: 100%;
    background-color: #06062B;
    color: #fff;

    .statistic {
      padding-top: 35px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }
  }

  .tabsView {
    padding: 30px;
    padding-bottom: 37px;

    .tabContainer {
      padding-top: 10px;
      color: #fff;

      .number {
        font-size: 30px;
        font-family: 'Heebo', sans-serif;
        font-weight: 400;
      }

      .name {
        position: relative;
        bottom: 10px;
        left: 20px;
        font-size: 14px;
        color: #8B959A;
        font-family: 'Heebo', sans-serif;
        font-weight: 400;
      }
    }

    .buy-block{
      padding-top: 15px;
      font-size: 15px;

      .percentage{
        color: #B158E1;
        padding-left: 10px;
      }

      .name{
        padding-left: 10px;
      }

      .buy-sell{
        border: 2px solid #B158E2;
        border-radius: 50px;
        color: #fff;
        padding: 3px 20px;

        & > span {
          text-transform: lowercase;
        }
      }
    }
  }
}

@media (max-width: 1234px) {
  .dashboard .overviewWrapper {
    .overview .tabsView .buy-sell {
      padding: 3px 10px;
    }

    .overview .tabsView .buy-block .percentage {
      padding-right: 10px;
    }
  }
}


@media (max-width: 1080px) {
  .dashboard .overviewWrapper {
    flex: 0 0 !important;

    .overview .transactions {
      padding-left: 10px;
    }

    .overview .wrapper {
      min-width: 240px;
    }

    .tabsView {
      padding: 30px 20px;
    }
  }
}

@media (max-width: 660px) {
  .dashboard .overviewWrapper {
     .overview .tabsView .buy-block .percentage {
       padding-right: 10px;
       margin: 0;
     }
  }
}


