.wallet {
  .walletItem {
    padding: 10px;
    height: 100px;
    background: #06062B;
    min-width: 120px;
    width: 100%;
    color: #fff;
    display: inline-block;
    cursor: pointer;

    .rowItem:first-child {
      padding-bottom: 20px;
    }

    .shortName {
      font-size: 12px;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }

    .fullName {
      font-size: 16px;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }

    .coint {
      font-size: 29px;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
      line-height: 1;
    }
  }
}

.wallet.active {
  width: 200px;
  background: #293C8B !important;
}

.wallet.active .walletItem {
  background: #293C8B !important;
}
