.background-wrapper {
  .login-sing {
    #form {
      .ema {
        width: 55%;
        height: 40px;
        border-radius: 7px;
        margin-top: 15px;
        border: 1px solid white;
        padding-left: 25px;
        margin-left: 15px;
      }

      .nem {
        width: 40%;
        height: 40px;
        border-radius: 7px;
        margin-top: 15px;
        border: 1px solid white;
        padding-left: 25px;
      }
    }
  }

  .login.login-sing {
    padding-right: 0;
  }
}

@media screen and (max-width: 425px) {
  .background-wrapper {
    .login-sing {
      .form-sing#form {
        max-width: 295px;
        margin-left: 10px !important;
      }
    }
  }
}


@media screen and (max-width: 374px) {
  .background-wrapper {
    .login-sing {
      .form-sing#form {
        margin-left: 5px !important;
      }
    }
  }
}
