.NewsVideo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: #06062B !important;
  height: 285px;
  position: relative;

  .CardMedia {
    height: 195px;
    width: 100%;

  }

  .Typography {
    font-size: 20px;
    line-height: 29px;
    color: #FFFFFF;
    margin: 30px 0;
    font-family: Heebo, sans-serif;
    font-weight: 100;
  }

  .IconButton {
    margin: 145px -60px;
    position: absolute;
    width: 65px;
    height: 65px;
    background-color: #FFFFFF;

    .playIcon {
      width: 30px;
      height: 30px;
      color: #000000;
    }
  }
}
