.wire-transfert{

    .content{
        width: 70%;
        margin: 0 auto;

        .selectCurrency > div > div{
            height: 25px;
        }

        .selectandinput{
            & > div:last-child{
                margin-left: 10px;
            }
        }

        .continueBtn{
            text-transform: capitalize;
            font-size: 16px;
            color: #fff;
            padding: 9px 20px;
            background-color: #B158E2;
            font-weight: 400;
            width: 100%;
            margin-top: 30px;
    
            &:hover {
              background-color: #B158E2;
            }
        }
    

        .label {
            text-transform: uppercase;
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 18px;
            color: #FFFFFF;
            margin: 11px 0;
        }
    
        input{
            color: #000000;
            background: #F3F4F5;
            border: 0;
            border-radius: 2px;
            padding: 11px 14px;
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 18px;
            width: 100%;
            box-shadow: 0 0 10px rgba(71, 71, 71, 0.1)
        }
    }
}