.market {
  width: 100%;

  .marketItem {
    padding: 20px;
    background: #06062B;
    color: #fff;

    .fromTo {
      color: #8B959E;
      font-size: 12px;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }

    .coint {
      color: #fff;
      font-weight: bold;
      font-size: 20px;
      padding-right: 10px;
      font-family: 'Heebo', sans-serif;
    }

    .currency {
      color: #fff;
      font-size: 20px;
      font-family: Heebo, sans-serif;
      font-weight: 400;
    }

    .volume {
      .percentage {
        background: transparent;
        padding: 0;
      }
    }

    .firstRow {
      padding-bottom: 20px;

      & > div {
        width: 50%;
        flex-wrap: nowrap;
      }
    }

    .volume {
      font-size: 14px;
      color: #fff;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;

      .amount {
        padding: 0 5px;
      }

      .percentage {
        color: #7AC231;
        font-size: 12px;
        font-family: 'Heebo', sans-serif;
        font-weight: 400;
      }
    }
  }

  .chartMarket {
    .recharts-cartesian-grid {
      display: none;
    }

    .recharts-layer.recharts-cartesian-axis-tick {
      display: none;
    }

    .recharts-wrapper, .recharts-surface {
      position: absolute;
      width: 100% !important;
      height: 100% !important;
      right: 10px;
      top: 5px;
    }


    .recharts-responsive-container {
      height: 150px !important;
    }

  }

  .marketItemSmall {


    .percentage {
      padding: 0;
      background: transparent;
      opacity: 0.3;
    }

    .leftBlock {
      width: 150px;

      .fromTo, .coint {
        font-weight: bold;
        font-size: 12px;
        color: #fff;
        font-family: 'Heebo', sans-serif;
        padding-right: 5px;
        text-transform: uppercase;
      }

      .currency {
        font-size: 12px;
        color: #fff;
        font-family: 'Heebo', sans-serif;
        font-weight: 400;
        text-transform: uppercase;
      }

      .percentage {
        font-size: 12px;
        color: #8B959E;
        font-family: 'Heebo', sans-serif;
        font-weight: 400;
      }
    }

    .rightBlock {
      .recharts-tooltip-wrapper {
        color: #fff;
      }

      circle.recharts-dot {
        display: none;
      }

      .lineChart .recharts-line-curve {
        stroke-width: 1;
      }

      .recharts-responsive-container {
        width: 130px !important;
        height: 70px !important;
      }

    }
  }
}
