.SettingsInfo {
  font-family: Heebo, sans-serif;
  font-weight: 400;

  .avatar {
    margin-top: 20px;
    height: 167px;
    width: 167px;
    border-radius: 2px;
    background: #8B959E;
  }

  .block {
    max-width: 57.333333%;
    flex-basis: 57.333333%;

    .uploadInput {
      display: none;
    }

  }

  h3 {
    font-size: 16px;
    color: #FFFFFF;
    font-family: Heebo, sans-serif;
    font-weight: 400;
  }

  p {
    color: rgba(139, 149, 158, 0.3);
    margin: 0;
  }

  .Input {
    width: 98%;
    background: #F3F4F5;
    border-radius: 2px;
    height: 40px;
  }

  input:after, input:before {
    display: none !important;
  }

  .inputs {
    background: #fff;
    border-radius: 2px;
    margin-left: 0;
    padding: 4px 0px;

    input {
      text-indent: 1em;
    }

  }

  .iconInBtn {
    padding-right: 15px;
  }

  .PersonalDetails {
    padding-top: 30px;

    .label {
      width: 100%;


      p {
        color: #fff;
        font-size: 12px;
        font-family: Heebo, sans-serif;
        font-weight: 400;
        padding-bottom: 10px;

        &:last-of-type {
          padding-top: 25px;
        }
      }
    }
  }

  .btnSave {
    margin-top: 25px;
    background: #B158E2;
    text-transform: none;
    color: #FFF;
    font-family: Heebo, sans-serif;
    font-weight: 400;
    padding: 8px 30px;

    &:hover {
      background: #B158E2;
    }
  }
}

@media screen and(max-width: 1023px) {
  .SettingsInfo {
    max-width: 100% !important;
    flex-basis: 100% !important;

    .btnSave {
      margin-top: 0;
    }

    .nikeName, .email {
      max-width: 100%;
      flex-basis: 100%;
    }

    .input {
      max-width: 100%;
      flex-basis: 100%;
      margin-top: 20px;
      margin-bottom: 30px;
    }
  }

  .SettingsList {
    width: 100% !important;

    div > ul {
      display: none;
      position: absolute;
    }

    .progressBar {
      width: 100% !important;
      margin-top: 20px !important;
    }
  }

  .PersonalDetails {
    .personalDetails, .legalName {
      max-width: 100%;
      flex-basis: 100%;
    }

    .birthBlock {
      justify-content: flex-start;

      .birthday {
        max-width: 100%;
        flex-basis: 100%;
        height: 125px;
      }

    }
  }

  .avatarBlock {
    .close {
      position: absolute;
      z-index: 100;
      top: 160px;
      right: 0;
      width: 45%;
    }

    .SettingsList {
      div > ul {
        display: flex;
        flex-direction: column;
        position: absolute;
        background: #000030;
        right: 0;
        z-index: 99;
        width: 50%;
      }

      .progressBar {
        display: none;
      }

      .listItem {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        padding-right: 20px;

        .Link {
          color: rgba(139, 149, 158, 0.8);
        }
      }
    }

    .iconMore {
      color: #fff;
      font-size: 22px;
      position: absolute;
      top: 104px;
      right: 20px;
    }
  }
}

@media screen and(max-width: 767px) {
  .avatarBlock {

    .block {
      max-width: 100%;
      flex-basis: 100%;

      & > div:first-child {
        width: 150px;
        position: absolute;
        top: 190px;
        left: 250px;
      }

      .btnSave {
        margin-top: 10px;
      }

      & > div:last-child {
        width: 250px;

        & > div {
          max-width: 100%;
          flex-basis: 100%;
        }

      }

    }
  }
}

@media screen and(max-width: 425px) {
  .SettingsInfo .avatar {
    height: 100px;
    width: 100px;

    svg {
      width: 30px;
    }
  }

  .avatarBlock .block {
    margin-top: 30px;

    & > div:first-child {
      top: 150px;
      left: 150px;
    }
  }
}

