.walletDetail {

  .walletItem {
    padding: 20px;
    background: #06062B;
    flex: 1 1;
    color: #fff;
    margin-bottom: 20px;
    display: inline-block;
    min-height: 250px;


    .shortName {
      font-size: 12px;
      text-transform: uppercase;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }

    .currency {
      opacity: 0.3;
    }

    .fullName {
      font-size: 20px;
      padding-left: 15px;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }

    .amount {
      font-size: 35px;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }
  }

  .chartWallet {

    .recharts-cartesian-grid {
      display: none;
    }

    .recharts-layer.recharts-cartesian-axis-tick {
      display: none;
    }

    .recharts-wrapper, .recharts-surface {
      position: absolute;
      width: 100% !important;
      height: 100% !important;
      right: 10px;

    }


    .recharts-responsive-container {
      width: 100%;
      height: 150px !important;
    }


  }
}


