.CalculatorMain {
  .textField {
    text-overflow: ellipsis;
    color: #06062B;
    background: #FFFFFF;
    width: 100%;
    border-radius: 2px;
    font-style: normal;
    font-weight: 300;
    font-size: 30px;

    input[type='number'] {
      -moz-appearance: textfield;
    }

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
      -webkit-appearance: none;
    }
  }

}
