.NewsMain {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-bottom: 35px;
}

@media screen and(max-width: 1023px) {
  .NewsMain {
    .firstSection {
      max-width: 100% !important;
      flex-basis: 100% !important;

      .first {
        padding-bottom: 20px;
      }

      .ReadMore {
        margin-right: 0;
      }
    }

    .secondSection {
      max-width: 100%;
      flex-basis: 100%;
      padding-top: 20px;

      .NewsScience {
        margin-right: 0;
      }
    }
  }
}


@media screen and(max-width: 767px) {
  .NewsMain {
    .firstSection {
      .first {
        max-width: 100%;
        flex-basis: 100%;
      }

      .readMore2 {
        max-width: 100%;
        flex-basis: 100%;
      }

      .ReadMore2 .ReadMore2Typography .ReadMore2Tittle {
        padding-bottom: 40px;
      }

      .ReadMore {
        margin: 0;
      }

      .NewsScience {
        margin: 0;
      }

      .newScience {
        max-width: 100%;
        flex-basis: 100%;
        margin-top: 20px;
      }
    }
  }
}


