.NewsDashBoard {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #06062B !important;
  height: 100%;

  .NewsDashBoardImage {
    height: 238px;
  }

  .NewsDashBoardContent {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .NewsDashBoardTittle {
      font-weight: 300;
      font-size: 20px;
      line-height: 145%;
      color: #FFFFFF;
      margin-bottom: 20px;
      font-family: Heebo, sans-serif;
      font-weight: 100;

    }

    .NewsDashBoardText {
      margin-bottom: 14px;
      font-weight: 300;
      font-size: 14px;
      line-height: 25px;
      color: #FFFFFF;
      font-family: Heebo, sans-serif;
      font-weight: 100;

    }
  }
}
