.lineChart {
  .yAxis, .xAxis {
    font-size: 10px;
    color: #8B959E;
  }

  .recharts-line-curve {
    stroke: #7AC231;
    stroke-width: 2;
  }

  .recharts-cartesian-grid-horizontal {
    line {
      stroke-width: 1px;
      stroke: #8B959E;
      opacity: 0.3;
    }
  }

  .recharts-responsive-container {
    width: 100%;
    height: 200px;
  }

  .recharts-cartesian-grid-vertical {
    line {
      display: none;
    }
  }

  .recharts-cartesian-axis-line {
    display: none;
  }

  .recharts-cartesian-axis-tick-line {
    opacity: 0.3;
  }

  .yAxis .recharts-cartesian-axis-tick-line {
    display: none;
  }

  .recharts-default-tooltip {
    background-color: #293C8B !important;
    border: none !important;
  }

  .recharts-cartesian-axis-tick-value {
    opacity: 0.3;
    font-size: 12px;
  }
}
