.sendCoin {
  padding-left: 25px;

  .wrapper {
    background-color: #06062B;
    color: #fff;
    padding: 20px;

    span {
      text-transform: uppercase;
      font-size: 12px;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }

    .amount, .selectName, .sentTo {
      border: 0;
      background: #F3F4F5;
      border-radius: 5px;
      width: 90px;
      height: 30px;
      margin-left: 10px;
      text-indent: 10px;

      &:after, &:before {
        display: none;
      }
    }

    .amount {
      padding-left: 10px;
    }

    .selectName > div {
      background: transparent;
    }

    .selectName > div:before, .selectName > div:after {
      border-bottom: 0 !important;
    }

    span:nth-child(3) {
      padding-left: 30px;
    }

    span:nth-child(2) {
      padding-right: 5px;
    }

    .sentTo {
      padding: 0 10px;
      width: 260px;
    }

    .firstRow {
      padding-bottom: 25px;
    }

    .btnContainer {
      padding-top: 30px;
    }

    .sendBtn {
      border: 2px solid #B158E2;
      border-radius: 50px;
      color: #fff;
      padding: 3px;

      & > span {
        text-transform: lowercase;
      }
    }
  }
}

@media (max-width: 375px) {
  .sendCoin .wrapper span:nth-child(3) {
    padding-left: 10px;
  }

  .dashboard .sendCoin .wrapper span {
    font-size: 9px;
  }

  .sendCoin .wrapper .amount, .sendCoin .wrapper .selectName{
    width: 120px;
  }
}
