.walletList {
  .active .wallet .walletItem {
    width: 200px;
    background: #293C8B;
  }

  .walletItem {
    margin-right: 10px;
    flex: 1 1;

    &:last-child {
      margin-right: 0;
    }

  }

  .title {
    font-size: 16px;
    color: #fff;
    font-family: 'Heebo', sans-serif;
    font-weight: 400;
  }
}
