.walletListDetails {
  .walletsItems {
    cursor: pointer;

    .linkToInfo {
      text-decoration: none;
    }
  }

  .addNewItem {
    border: 1px solid #8B959E;
    display: inline-block;
    width: 32%;
    cursor: pointer;
    min-height: 250px;
    opacity: 0.3;
    margin-bottom: 20px;
    box-sizing: border-box;

    .addWrapper {
      height: 100%;
    }

    .addWallet {
      color: #8B959E;
      font-size: 14px;
      text-align: center;
      position: relative;
      margin: 0;
      margin-top: 30px;
    }

    .addPlus {
      background: #8B959E;
      height: 50px;
      width: 1px;
      position: relative;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      margin: 0 auto;

      &:after {
        content: "";
        height: 1px;
        width: 50px;
        background: #8B959E;
        position: absolute;
        left: -25px;
        top: 25px;
        right: 0;
        bottom: 0;
        margin: 0 auto;
      }
    }
  }

  .transactionItems {
    width: 32%;
    margin-right: 14px;
  }
}

@media (max-width: 1205px) {
  .walletListDetails .transactionItems {
    width: 31%;
  }
}

@media (max-width: 1100px) {
  .walletDetail .walletItem .amount {
    font-size: 24px !important;
  }

  .transaction .transactionItem .time, .transaction .transactionItem .date, .transaction .transactionItem .address {
    font-size: 10px;
  }

  .transaction .transactionItem .shortName, .transaction .transactionItem .count {
    font-size: 10px;
  }

  .walletDetail .walletItem .shortName {
    font-size: 10px !important;
  }

  .transaction .transactionItem .rowItem:first-child {
    padding-bottom: 10px;
  }
}

@media (max-width: 1023px) {
  .walletListDetails .transactionItems {
    width: 48%;
    margin-right: 14px;
  }

  .walletListDetails .addNewItem {
    width: 47%;
  }
}

@media (max-width: 780px) {
  .walletListDetails .transactionItems {
    width: 100%;
    margin-right: 0;
  }

  .walletListDetails .addNewItem {
    width: 100%;
  }
}
