.newsMain {
  margin-top: 20px;

  .title {
    font-size: 16px;
    color: #fff;
    margin: 0;
    font-family: 'Heebo', sans-serif;
    font-weight: 400;
  }

  .wrapper {
    background-color: #06062B;
    color: #fff;
    padding: 20px;

    .article {
      font-size: 14px;
      padding: 15px 0 5px 0;
      margin: 0;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }

    .date {
      font-size: 12px;
      color: #8B959E;
      margin: 0;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }
  }
}
