.ReadMore2 {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;

  .ReadMore2Media {
    height: 100%;
  }

  .ReadMore2Typography {
    margin-top: 100px;
    margin-left: 5%;
    height: 100%;
    display: flex;
    flex-direction: column;
    font-weight: bold;
    font-style: normal;
    color: #FFFFFF;

    .ReadMore2Date {
      margin-bottom: 30px;
      font-family: Heebo, sans-serif;
      font-weight: bold;
      font-size: 12px;
      line-height: 31px;
      color: inherit;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }

    .ReadMore2Tittle {
      text-overflow: ellipsis;
      width: 80%;
      font-style: normal;
      font-weight: 300;
      font-size: 30px;
      line-height: 133%;
      font-family: Heebo, sans-serif;
      font-weight: 100;
      color: #FFFFFF;
    }

  }

}
