.deposit{
    .content{
        width: 70%;
        margin: 0 auto;

        .message-block{
            background: #293C8B;
            padding: 10px 30px 20px 20px;
            margin-bottom: 20px;
            border-radius: 3px;
            
            p{
                color: #fff;
                font-size: 12px;
                line-height: 18px;
            }
        }

        .referenceInput{
            position: relative;

            .copy-icon{
                width: 20px;
                height: auto;
                position: absolute;
                right: 20px;
                bottom: 7px;
            }

            input{
                padding: 11px 44px 11px 11px;
            }
        }

        #referenceNumber{
            width: auto;
            margin: 0;
            margin-right: 10px;
        }

        .checkbox{
            position: absolute;
            z-index: -1;
            opacity: 0;
            margin: 10px 0 0 20px;
        }

        .checkbox + label {
            position: relative;
            padding: 0 0 0 30px;
            cursor: pointer;
        }

        .checkbox + label:before {
            content: '';
            position: absolute;
            top: 1px;
            left: 0;
            width: 12px;
            height: 12px;
            background: transparent;
            border: 1px solid #fff;
            transition: .2s;
        }

        .checkbox + label:after {
            content: '';
            position: absolute;
            top: 4px;
            left: 3px;
            width: 22px;
            height: 22px;
            background: url("../../../../../../images/cross.png");
            background-repeat: no-repeat;
            transition: .2s;
        }

        .checkbox:checked + label:before {
            background: transparent;
            border: 1px solid #fff;
        }

        .checkbox:checked + label:after {
            display: none;
        }

        .reference-block{
            padding-top: 10px;

            label{
                margin-bottom: 0;
            }
        }

        .continueBtn{
            text-transform: capitalize;
            font-size: 16px;
            color: #fff;
            padding: 9px 20px;
            background-color: #B158E2;
            font-weight: 400;
            width: 100%;
            margin-top: 20px;
    
            &:hover {
                background-color: #B158E2;
            }
        }
    

        .label {
            text-transform: uppercase;
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 18px;
            color: #FFFFFF;
            margin: 11px 0;
        }
    }

}