.percentage {
  padding: 15px 25px;
  background: #06062B;
  border-radius: 5px;
  flex: 1;

  .CircularProgressbar {
    width: 40px;

    .CircularProgressbar-path {
      stroke: #B158E2;
    }

    .CircularProgressbar-trail {
      stroke: #293C8B;
    }

    .CircularProgressbar-text {
      display: none;
    }
  }

  .percentCount {
    color: #fff;
    font-size: 24px;
    margin: 0;
    margin-left: 25px;
    font-family: 'Heebo', sans-serif;
    font-weight: 400;
  }

  .percentName {
    color: #fff;
    margin: 0;
    margin-left: 20px;
    margin-right: 40px;
    font-size: 12px;
    font-family: 'Heebo', sans-serif;
    font-weight: 400;
  }

  .rowPerc {
    padding-bottom: 20px;
    position: relative;

    &:last-child {
      padding-bottom: 0;
    }

    .moreVert {
      color: #B158E2;
      position: absolute;
      right: 0;
    }
  }
}
