@import "../../../../index";

.buy-sell-wrapper{

  .wrapper{
    width: 100%;
    background-color: $dark-blue-paper;
    color: $white-color;
    position: relative;

    .close-icon{
      color: $white-color;
      position: absolute;
      right: 15px;
      top: 15px;
    }

    & > header{
      box-shadow: none;
      z-index: 0;
    }

    & > header > div > div> div > span {
        background-color: transparent;
    }

    & > header > div > div> div> div {

      & > button {
        min-width: 50%;
        opacity: 1;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        height: 77px;

        & > span{
          width: auto;
        }

        &[aria-selected="false"] {
          background-color: $active-tab;
        }

        &[aria-selected="true"] {
          background-color: $dark-blue-paper;
        }

        & > span > span > span {
          text-transform: capitalize;
          font-weight: 400;
          font-size: 30px;
          line-height: 44px;
          color: $white-color;
          position: relative;

          &:after{
            content: 'Currency';
            position: absolute;
            font-size: 12px;
            bottom: -36px;
            left: 0;
          }
        }

        & > span > span {
          position: relative;
          top: -10px;
        }
      }
    }
  }

  .title{
    font-weight: 400;
    font-size: 16px;
    line-height: 23px;
    color: $white-color;
  }

  .tab-content{
    width: 50%;
    margin-bottom: 40px;
    
    .message{
      color: rgba(139, 149, 158, 0.3);
      font-weight: 400;
      font-size: 12px;
      line-height: 18px;
      padding: 10px 0;
    }

    .form-control{
      width: 100%;
      padding-top: 70px;
      padding-bottom: 130px;
      
      .add-payment{
        color: $white-color;
        font-size: 12px;
        line-height: 18px;
        width: 100%;
        cursor: pointer;
      }
      
      .add-icon{
        color: $white-color;
        padding-right: 10px;
      }

      .continueBtn{
        text-transform: capitalize;
        font-size: 16px;
        color: $white-color;
        padding: 9px 20px;
        background-color: $pink-color;
        font-weight: 400;
        width: 100%;

        &:hover {
          background-color: $pink-color;
        }
      }

      .spacer{
        height: 14px;
        width: 100%;
      }
    }

    .form-wrapper{
      width: 70%;
      margin: 0 auto;

      .label{
        font-size: 16px;
        line-height: 18px;
        text-transform: uppercase;
        color: $white-color;
        padding-bottom: 15px;
      }

      & > div{

        width: 100%;
      }

      & > div > div{
        width: 49%;
      }

      .input-field{
        width: 49%;

        .amount{
          background: $white-color;
          width: 90%;
          border: 0;
          height: 30px;
          border-radius: 3px;
          padding: 8px;
          font-size: 16px;
          margin-top: 15px;
        }
      }
    }

    .selectCurrency{
      background: $white-color;
      border-radius: 3px;

      & > div > div{
        height: 30px;
        border-radius: 3px;
        padding: 11px 8px 4px 8px;
      }
    }
  }

  .right-block{
    width: 50%;

    .buy-info{
      color: $pink-color;
      font-size: 30px;
      line-height: 44px;
      padding-bottom: 50px;

      & > span:last-child{
        padding-left: 10px;
      }
    }

    .title{
      width: 100%;
      text-align: center;
      margin-top: 50px;
    }
    
    .wallet-info{
      color: $white-color;
      width: 60%;
      margin: 0 auto;
      position: relative;
      padding-left: 100px;
      margin-bottom: 50px;

      .label{
        color: $white-color;
        width: 100%;
      }

      &:before{
        content: url("../../../../images/buy.png");
        position: absolute;
        top: 30px;
        left: 50px;
      }
    }

    .wallet-info.info-sell{
      &:before{
        content: url("../../../../images/sell.png");
        position: absolute;
        top: 30px;
        left: 50px;
      }
    }

    .border-block{
      width: 60%;
      border: 2px solid $active-tab;
      margin: 0 auto;
      color: $white-color;
      font-size: 14px;

      .line{
        width: 100px;
        height: 1px;
        background-color: $active-tab;
        display: flex;
        flex: 1 1;
        margin: 0 10px;
      }

      & > p{
        padding: 8px;
        display: flex;
        flex-direction: row;
        align-items: flex-end;
        justify-content: space-between;
      }
    }
  }
}

@media (max-width: 1130px) {
  .buy-sell-wrapper{
    .wrapper > header > div > div > div > div > button > span > span > span{
      font-size: 22px;
    }
  }
}

@media (max-width: 1023px) {
  .buy-sell-wrapper{
    .tab-content .form-wrapper {
      width: 90%;
    }

    .right-block{
      .wallet-info {
        width: 90%;
      }

      .border-block {
        width: 90%;
      }
    }
  }
}


@media (max-width: 767px) {
  .buy-sell-wrapper{
    .tab-content {
      width: 100%;
    }

    .right-block {
      width: 100%;
      padding-bottom: 50px;
    }

    .wrapper .close-icon{
      right: 5px;
      top: 5px;
    }
  }
}

@media (max-width: 424px) {
  .buy-sell-wrapper{
    .tab-content .form-wrapper .label {
      font-size: 11px;
    }

    .tab-content .selectCurrency {
      font-size: 13px;
    }

    .tab-content .form-control {
      padding-bottom: 70px;
    }

    .wrapper > header > div > div > div > div > button > span > span > span {
      font-size: 20px;
    }

    .right-block {
      .border-block {
        font-size: 12px;
      }

      .buy-info {
        padding-bottom: 25px;
      }

      .wallet-info {
        width: 100%;
      }
    }
  }
}

.buy-sell-wrapper.card-bank-wrapper .wrapper > header > div > div > div > div > .card-tab > span > span > span:after{
  content: 'Link your card';
  width: 80px;
}

.buy-sell-wrapper.card-bank-wrapper .wrapper > header > div > div > div > div > .bank-tab > span > span > span:after{
  content: 'Wire Transfert';
  width: 77px;
}

buy-sell-wrapper.bank-account.wrapper > header > div > div > div > div > .bank-tab > span > span > span:after{
  content: 'Deposit';
  width: 77px;
}