.SettingsList {

  .listItem {
    padding: 0;
    margin-top: 31px;

    .Link {
      text-decoration: none;
      font-size: 12px;
      line-height: 18px;
      text-transform: uppercase;
      color: rgba(139, 149, 158, 0.3);
      font-family: Heebo, sans-serif;
      font-weight: 400;

      &:hover {
        color: #fff;
      }
    }

    .active{
      color: #fff;
    }
  }


  .progressBar {
    border-radius: 3px;
    font-size: 16px;
    line-height: 23px;
    text-align: center;
    color: #FFFFFF;
    width: 248px;
    background-color: #06062B;
    height: 165px;
    margin-top: 100px;
    padding: 10px;
    font-family: Heebo, sans-serif;
    font-weight: 400;

    h3 {
      font-family: Heebo, sans-serif;
      font-weight: 400;
    }


    .Bar {
      height: 16.34px;
      width: 100%;
      background-color: #65656A;
      border-radius: 7.5px;
      display: flex;
      flex-direction: row-reverse;

      .indicator {
        border-radius: 0 8px 8px 0;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 16.34px;
        background-color: #293C8B;
        text-align: center;
        color: #FFFFFF;
        font-size: 12px;
      }
    }

    .update {
      color: #B158E2;

      & > span {
        text-transform: capitalize;
        font-family: Heebo, sans-serif;
        font-weight: 400;
      }
    }
  }
}

