.transactionWrapper {
  flex: 1;

  .title {
    font-size: 16px;
    color: #fff;
    font-family: 'Heebo', sans-serif;
    font-weight: 400;
  }

  .smallTransaction {
    background: #06062B
  }

  .tabsView {
    padding-top: 20px;

    .tabContainer {
      color: #fff;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }

    header {
      background-color: transparent;
      overflow: hidden;
      padding-left: 20px;
      box-shadow: none;

      & > div > div > div > div {
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;

        & > button {
          min-width: auto;
          opacity: 1;

          & > span > span > span {
            color: #fff;
            font-size: 12px;
            font-family: 'Heebo', sans-serif;
            font-weight: 400;
          }

          &[aria-selected="true"] > span > span > span {
            color: #B158E2;
            font-size: 12px;
          }

          & > span > span {
            padding: 0 5px;
          }

          & > span:last-child {
            display: none;
          }
        }
      }

      & > div > div > div > span {
        background-color: transparent;
      }
    }
  }

  .btn {
    padding: 20px;

    .viewBtn {
      border: 2px solid #B158E2;
      border-radius: 50px;
      color: #fff;
      padding: 0px 10px;

      & > span {
        text-transform: capitalize;
        font-family: 'Heebo', sans-serif;
        font-weight: 400;
      }
    }
  }
}

.transactionBigWrapper {
  flex: 1;
  height: calc(100% - 105px);

  .title {
    font-size: 16px;
    color: #fff;
    font-family: 'Heebo', sans-serif;
    font-weight: 400;
  }

  .smallTransaction {
    background: #06062B;
  }

  .tabsView {
    padding-top: 20px;

    & > div {
      padding-bottom: 15px;
    }

    .information {
      width: 50%;
      display: flex;
      flex-direction: row;
      align-items: center;

      .count {
        color: #fff;
        font-size: 34px;
        font-family: 'Heebo', sans-serif;
        font-weight: 400;
        padding-right: 10px;
      }

      .countOf {
        color: #fff;
        font-size: 12px;
        padding: 0 25px 5px 10px;
        font-family: 'Heebo', sans-serif;
        font-weight: 400;
        display: flex;
        align-items: center;
        flex-direction: row;
      }

      .export {
        text-transform: capitalize;
        font-size: 12px;
        color: #000;
        font-family: 'Heebo', sans-serif;
        font-weight: 400;

      }
    }

    .tabContainer {
      color: #fff;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }

    header {
      background-color: transparent;
      overflow: hidden;
      padding-left: 20px;
      box-shadow: none;
      width: 50%;
      z-index: 0;

      & > div > div > div > div {
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;

        & > button {
          min-width: auto;
          opacity: 1;

          & > span > span > span {
            color: #fff;
            font-size: 12px;
            font-family: 'Heebo', sans-serif;
            font-weight: 400;

          }

          &[aria-selected="true"] > span > span > span {
            color: #B158E2;
            font-size: 12px;
            font-family: 'Heebo', sans-serif;
            font-weight: 400;

          }

          & > span > span {
            padding: 0 5px;
          }

          & > span:last-child {
            display: none;
          }
        }
      }

      & > div > div > div > span {
        background-color: transparent;
      }
    }
  }

  .transactionList > div {
    padding: 0;
  }

  .date {
    width: 150px;
  }

  .icon {
    width: 35px;
  }

  .shortName {
    padding-right: 30px;
  }

  //.bigTransactions > div > div{
  //  width: 205px;
  //}

  .react-datepicker-popper:last-child {
    margin-left: -70px;

    .react-datepicker__triangle {
      left: 180px !important;
    }
  }
}

@media (max-width: 1080px) {

  .transactionBigWrapper {

    .bigTransactions > div > div {
      width: 190px;
    }

    .shortName {
      padding-right: 15px;
    }

    .date {
      width: 90px;
    }

    .datePicker {
      padding-top: 0;
    }
  }
}

@media (max-width: 1023px) {
  .transactionBigWrapper {

    .tabsView header {
      width: 100%;
      position: absolute;
      top: 90px;
      right: 20px;
    }

    .react-datepicker-popper {
      left: -70px !important;
    }

    .datePicker {
      justify-content: flex-start;

      .period:first-of-type {
        padding-left: 0;
      }
    }
  }
}


@media (max-width: 425px) {
  .transaction .bigTransactions .firstRow .address {
    font-size: 12px;
    width: 100%;

    svg {
      margin: 0;
      width: 20px;
    }

  }

  .transaction .bigTransactions {
    padding: 10px !important;

    .count, .shortName {
      font-size: 18px !important;
    }
  }

  .transactionList {
    min-width: 100% !important;
  }
}
