.datePicker {
  padding-top: 20px;

  .period {
    color: #fff;
    text-transform: uppercase;
    font-size: 14px;
    padding-right: 10px;
    padding-left: 20px;
    font-family: 'Heebo', sans-serif;
    font-weight: 400;
  }

  .pickerInput {
    padding: 10px 12px;
    border: 0;
    border-radius: 5px;
    max-width: 105px;
    background-color: #F3F4F5;
  }

  .react-datepicker__input-container {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
  }
}

@media screen and(max-width: 1126px) {
  .datePicker .pickerInput {
    padding: 6px 12px;
  }
}

@media screen and(max-width: 500px) {

  .walletInfo .chartColumn .chartInfo .react-datepicker-popper:last-child {
    margin-left: -164px !important;
  }
}
