.background-wrapper {
  background: url("../../../../images/BG.png") no-repeat;
  background-color: black;
  background-size: cover;
  width: 100%;
  min-height: 100vh;
  height: 100%;
  background-attachment: fixed;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .top {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-top: 40px;

    .photo {
      width: 150px;
      height: 54px;
      background: url("../../../../images/logo2.png") no-repeat;
      background-size: cover;
      margin-left: 60px;
    }

    .sim {
      display: none;
      width: 35px;
      height: 13px;
      background: url("../../../../images/Group.png") no-repeat;
      background-size: cover;
      margin-right: 17px;
    }

    .supp {
      height: 50px;
      width: 200px;
      margin-right: 60px;
      font-size: 16px;
      display: flex;
      justify-content: space-around;
      color: #FFFFFF;
      align-items: center;

      .formlang {
        margin-bottom: 0;

        .lang {
          background: TRANSPARENT;
          color: white;
          border: 0;
          font-family: 'Heebo', sans-serif;
          font-weight: 400;
          font-size: 16px;
          outline: none;
        }
      }

      .su {
        text-decoration: none;
        color: #FFFFFF;
        font-family: 'Heebo', sans-serif;
        font-weight: 400;
      }
    }

  }

  .login {
    max-width: 310px;
    max-height: 300px;
    width: 100%;
    height: 100%;
    padding-bottom: 200px;
    margin-left: 220px;
    font-family: 'Heebo', sans-serif;
    font-weight: 300;

    #form {
      margin-top: 25px;
      margin-bottom: 0;

      input {
        width: 100%;
        height: 40px;
        border-radius: 7px;
        margin-top: 15px;
        border: 1px solid white;
        padding-left: 25px;
      }

      button {
        height: 40px;
        width: 50%;
        border-radius: 7px;
        margin-top: 15px;
        background: #B158E2;
        border: 1px solid #B158E2;

        .butcol {
          color: white;
        }
      }
    }

    .log {
      font-size: 30px;
      color: white;

    }

    .sig {
      margin-bottom: 0;
      margin-top: 30px;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;

      .sign {
        font-size: 12px;
        color: #8B959E;
        text-decoration: none;

        .up {
          color: #B158E2;
          text-decoration: none;
          padding-left: 10px;
        }
      }
    }
  }

  .bottom {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    //position: absolute;
    //bottom: 5%;

    .xz {
      width: 260px;
      padding-left: 130px;
      display: flex;
      justify-content: space-between;

      p {
        margin: 0;
      }
    }

    .contact {
      width: auto;
      padding-right: 60px;

      .consh {
        font-family: 'Heebo', sans-serif;
        font-weight: 400;
        font-size: 12px;

        .terms {
          color: #F3F4F5;
          text-decoration: none;
          font-size: 12px;
          padding-left: 20px;
        }
      }

    }

  }

}

@media screen and (max-width: 1024px) {
  .background-wrapper {

    .login {
      margin-left: 120px;
    }

    .bottom .xz {
      padding-left: 60px;
    }
  }
}

@media screen and (max-width: 767px) {
  .background-wrapper {

    .bottom {
      width: 100%;
      flex-direction: column;
      justify-content: center !important;
      align-items: center;

      .xz {
        width: 260px;
        height: 50px;
        margin-top: 0px !important;
        margin-left: 0px !important;
        display: flex;
        justify-content: space-between;
        padding-left: 0;
      }

      .contact {
        width: 260px;
        height: 50px;
        margin-top: 0 !important;
        padding-right: 0;
        text-align: center;

        .consh .terms {
          padding-left: 0;
          padding: 0 10px;
        }
      }
    }

    .login {
      margin: 0 auto;
      padding-right: 30px;
      margin-top: 70px;

      #form {
        margin-top: 50px;

        .nem, .ema, input {
          padding-left: 10px !important;
        }

        input {
          padding-right: 20px !important;
        }
      }

      #form button {
        margin-top: 30px;
        width: 338px;
      }

      .log {
        text-align: left;
      }

      .sig {
        text-align: center;

        .sing {
          padding-left: 35px;
          text-decoration: none;
        }
      }
    }

    .top {
      align-items: baseline;
      padding-top: 20px;

      .supp {
        display: none;
      }

      .supmob {
        position: absolute;
        top: 100px;
        right: 0px;

        .su1 {
          text-decoration: none;
          color: #FFFFFF;
          font-family: 'Heebo', sans-serif;
          font-weight: 400;
          padding-right: 15px;
        }

        .formlang {
          display: flex;
          flex-direction: row;
          justify-content: flex-end;

          .lang {
            background: transparent;
            color: white;
            border: 0;
            font-family: 'Heebo', sans-serif;
            font-weight: 400;
            font-size: 16px;
            outline: none;
          }
        }
      }

      .sim {
        display: block;
        margin-left: 30px;
      }

      .photo {
        margin-top: 0;
        margin-left: 30px;
      }
    }
  }
}


@media screen and (max-width: 425px) {
  .background-wrapper {
    .login {
      padding-right: 0px;

      .log {
        padding-left: 15px;
      }


      #form {
        max-width: 261px;
        padding-left: 10px;

        button {
          width: 295px;
        }
      }
    }
  }
}


@media screen and (max-width: 374px) {
  .background-wrapper {
    .login {
      #form {

        .ema{
          margin-left: 10px !important;
        }

        input{
          padding-right: 15px !important;
        }

        button {
          width: 290px;
        }
      }
    }
  }
}

