.drawer {
  width: 90px;

  & > div:first-child {
    background-color: transparent;
    width: 90px;
  }

  & > div:last-child {
    width: 90px;
    background: #06062B;

    ul {

      a {
        width: 100%;
        padding: 15px 0;
        cursor: pointer;

        & > div {
          margin-right: 0;
          width: 100%;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
        }

        &:focus {
          background-color: #B158E2;
        }
      }
    }

    .logo {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      padding: 35px 0 !important;
      cursor: pointer;

      &:focus {
        background-color: transparent;
        outline: none;
      }
    }
  }
}

.main {
  background: #000016;
  height: calc(100% - 105px);
  min-height: calc(100vh - 105px);
  margin-left: 90px;
  padding-left: 25px;
  padding-right: 40px;
  overflow: hidden;
}

@media screen and(max-width: 1023px) {
  .drawer {
    display: none;
  }

  .main {
    margin-left: 0;
    padding-left: 20px;
    padding-right: 20px;
  }
}
