.chart {
  max-width: 600px;
  width: 100%;

  .title {
    font-size: 16px;
    color: #fff;
    font-family: 'Heebo', sans-serif;
    font-weight: 400;
  }

  .wrapper {
    background-color: #06062B;
    color: #fff;
    padding: 20px 0;
    width: 100%;
    margin-top: 25px;

    .recharts-surface {
      width: 580px;
    }

    .headerContent {
      padding: 0 0 15px 30px;
      background: transparent;

    }
  }

  .recharts-responsive-container {
    width: 100%;
    height: 300px !important;
  }

  .radioBtnGroup {
    flex: 1;
    padding-right: 20px;
    max-width: 385px;

    input[type="radio"] {
      width: 50px;
      height: 30px;
      border-radius: 15px;
      -webkit-appearance: none;
      -moz-appearance: none;
      outline: none;

      &:focus {
        outline-color: transparent;
        background-color: #B158E2;
      }

      &:checked {
        background-color: #B158E2;
        outline-color: transparent;

        & ~ span:first-of-type {
          color: white;
        }
      }
    }

    label {
      width: 65px;
      height: 35px;
    }

    label span:first-of-type {
      position: relative;
      left: 19px;
      font-size: 12px;
      color: #fff;
    }

    label span {
      position: relative;
      top: -32px;
    }

  }

  .radioBtnGroupMob {
    display: none;

    .selectTab {
      color: #fff;
      font-size: 0.8em;
      max-width: 85px;
      cursor: pointer;
      background: #B158E2;
      border-radius: 50px;
      display: none;

      & > div:before,
      & > div:after {
        display: none !important;
      }
    }
  }
}


@media screen and(max-width: 1023px) {
  .chartInfo, .chartMarket {
    .chart {
      .wrapper .headerContent {
        justify-content: space-between;
        padding-right: 40px;
        padding-bottom: 90px;
      }

      .radioBtnGroup {
        display: none;

        label {
          display: none;
        }

        .selectTab {
          display: inline-block;
        }
      }

      .radioBtnGroupMob {
        display: inline-block;

        .selectTab {
          color: #fff;
          display: inline-block;

          & > div svg {
            display: none;
          }

          & > div:before,
          & > div:after {
            display: none;
          }

          & > div > div {
            padding: 10px 20px;
          }
        }

        .tabsBlock {
          display: flex;
          flex-direction: row;
          align-items: center;
        }

        .iconInfo {
          padding-left: 20px;
        }
      }

    }
  }
}
