@media screen and(max-width: 1023px) {
  .calculatorPage {
    flex-direction: column;

    & > div {
      max-width: 100%;
    }

    & > div:first-child {
      padding-bottom: 20px;
    }

    .marketListCalculator {
      width: 100%;

      .marketItems {
        margin-left: 0;
      }

      .marketItems .linkToTranding {
        width: 49%;
      }
    }
  }
}

@media screen and(max-width: 767px) {
  .calculatorPage .marketListCalculator .marketItems .linkToTranding {
    width: 100%;
  }

  .Converter {
    flex-direction: column;
    align-items: flex-start;

    .exchangeButton {
      width: auto;
      padding: 10px 0;
    }

    .text-fields {
      width: 100%;
    }
  }

  .CalculatorMain .textField div fieldset input {
    font-size: 17px;
  }
}
