.marketCalculator {

  .marketItem {
    padding: 20px;
    background: #06062B;
    color: #fff;
    //display: inline-block;

    .currency {
      color: #8B959E;
      opacity: 0.3;
      font-size: 14px;
      font-family: Heebo, sans-serif;
      font-weight: 400;
    }

    .fullName {
      color: #fff;
      font-family: Heebo, sans-serif;
      font-weight: 100;
      font-size: 20px;
    }

    .firstRow {
      padding-bottom: 20px;

      & > div {
        width: 50%;
        flex-wrap: nowrap;
      }
    }

    .datas {
      font-size: 14px;
      color: #fff;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;

      .amount {
        font-size: 30px;
        font-family: 'Heebo', sans-serif;
        font-weight: 400;
        line-height: 1;
      }

      .percentage {
        color: #7AC231;
        font-size: 14px;
        font-family: 'Heebo', sans-serif;
        font-weight: 400;
        background: transparent;
        padding: 0;
      }

      & > div {
        width: auto;
      }

    }

    .chartMarket {
      .recharts-cartesian-grid {
        display: none;
      }

      .recharts-layer.recharts-cartesian-axis-tick {
        display: none;
      }

      .recharts-wrapper, .recharts-surface {
        width: 100% !important;
        height: 100% !important;
        right: 17px;
      }

      //.recharts-tooltip-wrapper {
      //  display: none;
      //}

      .recharts-active-dot {
        display: none;
      }

      .recharts-responsive-container {
        height: 100px !important;
      }

    }
  }
}
