.day {
    position: relative;
    flex: 1 0 14.28%;
    max-width: calc(100% / 7);
    min-height: 130px;
}

.dayAddress {
    text-align: start;
    font-size: 12px !important;
    line-height: 18px !important;
    color: #FFFFFF !important;
    width: 70%;
    font-family: Heebo, sans-serif !important;
    font-weight: 400 !important;
}

.borderBox {
    display: flex;
    width: 90%;
    margin-top: 10%;
    border-top: #FFFFFF 2px solid;
    border-bottom: #979797 1px solid;
    align-self: start;
    justify-content: space-between;
    align-items: center;
}

.borderBox .dayCity {
    font-size: 12px;
    line-height: 18px;
    text-align: right;
    font-family: Heebo, sans-serif;
    font-weight: 400;
    color: rgba(139, 149, 158, 0.3);
}


.day.first-index-1 {
    margin-left: 14.28%;
}

.day.first-index-2 {
    margin-left: 28.56%;
}

.day.first-index-3 {
    margin-left: 42.84%;
}

.day.first-index-4 {
    margin-left: 57.12%;
}

.day.first-index-5 {
    margin-left: 71.4%;
}

.day.first-index-6 {
    margin-left: 85.68%;
}

.day.first-index-7 {
    margin-left: 99.96%;
}

.day header {
    font-weight: normal;
    font-size: 26px;
    line-height: 38px;
    color: #FFFFFF;
}

