
.card-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  padding: 60px 0px;
  box-sizing: border-box;

  .city-postal{
    margin-top: 10px;
    & > div > input{
      margin-top: 11px;
    }
  }

  .icons{
    position: absolute;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
    width: 100%;
    right: 15px;
  }

  label {
    text-transform: uppercase;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 18px;
    color: #FFFFFF;
    margin: 11px 0;
  }

  input {
    color: #000000;
    background: #F3F4F5;
    border-radius: 2px;
    padding: 11px 14px;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 18px;
    width: 100%;
    box-sizing: border-box;
    box-shadow: 0 0 10px rgba(71, 71, 71, 0.1);
    border: 0;
  }

  p{
    color: rgba(139, 149, 158, 0.3);
    font-size: 12px;
    line-height: 18px;
    margin-bottom: 0;
  }

  a{
    color: #B252E5;
    text-decoration: none;
  }

  .card-form-button {
    color: #FFFFFF;
    margin-top: 30px;
    width: 100%;
    padding: 10px 0;
    text-align: center;
    background-color: #B158E2;
    font-size: 16px;
    line-height: 23px;

    & > span {
      text-transform: capitalize;
    }
  }

  .card-form-button:hover {
    background-color: #B158E2;
  }

  .card-number {
    color: #000000;
    background: #F3F4F5;
    border-radius: 2px;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 18px;
    padding: 11px 14px;
    width: 100%;
  }

  .half-clock{
    width: 50%;
  }

  .card-expiry {
    color: #000000;
    background: #F3F4F5;
    border-radius: 2px;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 18px;
    padding: 11px 14px;
  }

  .security-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    padding-bottom: 11px;

    .security {
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;
      position: relative;



      .cvc {
        color: #000000;
        background: #F3F4F5;
        border-radius: 2px;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 18px;
        padding: 11px 10px;
        width: 100%;
        box-sizing: border-box;

      }
    }

    .cvc-clock{
      margin-left: 8px;
    }
  }
}
.card-result{

  display: flex;
  width: 100%;
  height: 100%;
  padding: 10% 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  h1{
    margin-top: 15%;
    font-size: 30px;
    line-height: 44px;
    text-align: center;
    color: #FFFFFF;
    height: 100%;
  }
  p{
    margin-top:20%;
    font-size: 12px;
    line-height: 18px;
    color: rgba(139, 149, 158, 0.3);

   a{
     font-size: 12px;
     line-height: 18px;
     color: #B158E2;
     text-decoration: none;
   }
  }
}
