.user {
  .avatar {
    border-radius: 5px;
    margin: 0 30px 0 20px;
    background-color: #8B959E;
  }

  .userName {
    color: #fff;
    margin-right: 90px;
    font-size: 14px;
    font-family: 'Heebo', sans-serif;
    font-weight: 400;
  }

  .supportBtn {
    text-transform: capitalize;
    font-size: 12px;
    color: #fff;
    padding: 9px 20px;
    background-color: #B158E2;
    font-family: 'Heebo', sans-serif;
    font-weight: 400;

    &:hover {
      background-color: #B158E2;
    }
  }

  .iconInfo {
    color: #fff;
    font-size: 12px;
    padding-right: 10px;
  }
}


@media screen and(max-width: 1023px) {
  .user {
    display: none !important;
  }
}
