.marketWrapper {
  flex: 1 1;

  .smallMarket {
    padding: 25px;
    background: #06062B;
    min-width: 300px;
    padding-top: 10px;
    margin-left: 20px;

    .marketList {
      display: flex;
      width: 100%;

      .marketItems {
        flex-direction: column;

        .market {
          width: 100%;
          padding-top: 10px;
          border-bottom: 1px solid #8B959E;

          &:first-of-type {
            padding-top: 0
          }
        }
      }
    }
  }

  .market-button {
    margin-top: 5px;
    color: #ffffff;
    font-size: 12px;
    font-family: 'Heebo', sans-serif;
    font-weight: 400;
  }

  .selected-button {
    margin-top: 5px;
    color: #B158E2;
    font-size: 12px;
    font-family: 'Heebo', sans-serif;
    font-weight: 400;
  }

  .title {
    font-size: 16px;
    font-family: 'Heebo', sans-serif;
    font-weight: 400;
    color: #fff;
    margin: 0;
    position: relative;
    top: 44px;
    padding-left: 45px;
    z-index: 1;
    width: 30%;
  }

  .tabsView {
    min-width: 100%;

    header {
      background-color: transparent;
      overflow: hidden;
      padding-left: 20px;
      box-shadow: none;
      z-index: 0;

      & > div > div > div > div {
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;

        & > button {
          min-width: auto;
          opacity: 1;

          & > span > span > span {
            color: #fff;
            font-size: 12px;
            font-family: 'Heebo', sans-serif;
            font-weight: 400;
          }

          &[aria-selected="true"] > span > span > span {
            color: #B158E2;
            font-size: 12px;
            font-family: 'Heebo', sans-serif;
            font-weight: 400;
          }

          & > span > span {
            padding: 0 5px;
          }

          & > span:last-child {
            display: none;
          }
        }
      }

      & > div > div > div > span {
        background-color: transparent;
      }
    }
  }
}


.marketBigWrapper {
  width: 100%;

  .linkToTranding {
    text-decoration: none;
    width: 23%;
    padding-right: 18px;
    margin-bottom: 20px;
  }
}

@media (max-width: 1060px) {
  .marketBigWrapper {
    .linkToTranding {
      width: 31%;
      padding-right: 16px;
    }
  }
}

@media (max-width: 767px) {
  .marketBigWrapper {
    .linkToTranding {
      width: 47%;
      padding-right: 15px;
    }
  }
}

@media (max-width: 625px) {
  .marketBigWrapper {
    .linkToTranding {
      width: 100%;
      padding-right: 0px;
    }
  }

}


@media (max-width: 400px) {

  .marketWrapper {

    .smallMarket {
      min-width: 225px;

      .selected-button , .market-button{
        min-width: 40px;
      }
    }
  }
}
