.wrapper {
  .react-stockchart {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    svg.react-stockchart {
      position: relative !important;
      width: 700px;
      height: 600px;
    }

    .title {
      color: #fff;
      font-size: 30px;
    }

    g rect {
      fill-opacity: 1 !important;
    }

    .react-stockcharts-axis-domain {
      stroke-width: 0;
    }

    .tick {
      text {
        fill: #8B959E;
        opacity: 0.3;
      }
    }

    .react-stockcharts-candlestick-wick {
      .up {
        stroke: #7AC231;
      }

      .down {
        stroke: #FF005C;
      }
    }

    .react-stockcharts-candlestick-candle {
      .up {
        fill: transparent;
        stroke: #7AC231;
        stroke-width: 1px;
      }

      .down {
        fill: #FF005C;
      }
    }

    .react-stockcharts-avoid-interaction {
      & > g:last-child {
        & > g:last-child {
          opacity: 0.3;
        }
      }
    }

    .recharts-cartesian-grid-horizontal line {
      stroke-width: 1px;
      stroke: #8B959E;
    }
  }
}
