.month-header {
  display: flex;
  align-items: center;

  .Icon {
    margin-left: 7px;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #8B959E;
    background: #F3F4F5;
    width: 29px;
    height: 29px;
    border-radius: 2px;
  }

  h1 {
    margin-right: 16px;
    font-size: 16px;
    line-height: 23px;
    color: #FFFFFF;
    font-family: Heebo, sans-serif;
    font-weight: 400;
  }

  .selectPeriod {
    padding-bottom: 20px;

    & > div {
      width: auto;
    }

    & div:last-child {
      padding-top: 0;
    }

    .datePicker {
      .period:first-of-type {
        padding-left: 0;
      }

      .react-datepicker-popper:last-child {
        margin-left: -150px;

        .react-datepicker__triangle {
          left: 180px !important;
        }
      }
    }
  }

}

@media screen and(max-width: 767px) {
  .month-header {
    width: 90%;
  }
}

@media screen and(max-width: 650px) {
  .month-header {
    width: 100%;

    .selectPeriod {
      padding-bottom: 20px;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;

      & > div:first-child {
        padding-bottom: 30px;
      }

      .datePicker {
        .period:first-of-type {
          padding-left: 0;
        }

      }
    }
  }
}





