.dashboard {
  padding-bottom: 50px;
  background: #000016;

  .overviewWrapper {
    display: flex;
    flex: 1 1;
  }

  .transactionBlock {
    flex: 1;

    .viewBtn {
      display: none;
    }
  }

  .secondRow {
    margin-top: 20px;
  }

  .middleBlock {
    margin: 0 0 0 25px;
    width: auto;
  }
}

@media (max-width: 1430px) {
  .dashboard {
    .mainBlock {
      width: 100%;
    }

    .middleBlock {
      width: 70%;
    }
  }
}

@media (max-width: 1160px) {
  .dashboard {
    .mainBlock {
      margin-right: 0 !important;

      &:last-child {
        flex-direction: column;
      }

      .market .marketItemSmall .rightBlock .recharts-responsive-container {
        max-width: 100% !important;
        width: 100% !important;
      }

      .chartMarket {
        flex-direction: column;
      }

      .middleBlock {
        margin: 0 !important;
        margin-left: 20px !important;
        display: flex;
        flex-direction: column;
      }
    }

    .chart {
      max-width: 100%;

      .wrapper .recharts-surface {
        width: 96%;
      }
    }

    .marketWrapper {
      .title {
        padding-left: 25px;
      }

      .smallMarket {
        margin-left: 0;
      }
    }

    .transactionWrapper .title {
      padding: 10px 0;
    }

    .percentage {
      padding: 15px;

      .percentCount {
        margin-left: 10px;
        font-size: 17px;
      }

      .percentName {
        margin-right: 0px;
      }
    }

    .wallet .walletItem {
      min-width: 100px;
    }
  }
}


@media (max-width: 1080px) {
  .middleBlock {
    flex: 1 1;
  }
}


@media (max-width: 1023px) {
  .dashboard {
    padding-left: 0px;
  }
}

@media (max-width: 880px) {
  .dashboard {
    .mainBlock {
      & > div {
        &:first-child {
          flex-direction: column;
        }
      }

      .middleBlock {
        width: 100%;
        margin-left: 0 !important;
      }
    }

    .overviewWrapper {
      flex: 1 1 !important;
      width: 100%;

      .statistic > div:first-child {
        justify-content: flex-start;
        align-items: flex-start;
        padding-left: 20px;
      }
    }
  }
}

@media (max-width: 660px) {
  .dashboard {
    .secondRow {
      flex-direction: column;
      justify-content: flex-start;
    }

    .percentage {
      width: 92%;
      margin-bottom: 20px;
    }

    .sendCoin {
      padding-left: 0;
      width: 100%;

      .wrapper {
        padding: 15px;

        .firstRow {
          justify-content: center;
        }

        & > div > div:nth-child(2) {
          display: flex;
          justify-content: center;
        }

        span {
          font-size: 11px;
        }
      }
    }

    .sendCoin .wrapper .amount, .sendCoin .wrapper .selectName, .sendCoin .wrapper .sentTo {
      margin-left: 5px;
    }

    .walletList .walletItem {
      margin-bottom: 10px;
    }

    .wallet .walletItem {
      margin-bottom: 0px;

      .coint {
        font-size: 18px;
      }

      .fullName {
        font-size: 14px;
      }
    }

    .wallet.active {
      max-width: 200px;
      width: 100%;
    }
  }
}

@media (max-width: 550px) {
  .dashboard .wallet.active {
    max-width: 100%;
    width: 100%;
  }
}



