.balance {
  margin-bottom: 20px;

  .wrapper {
    padding: 15px 25px;
    background: #06062B;
    border-radius: 5px;

    .total {
      color: #fff;
      padding-bottom: 5px;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }

    .amount {
      color: #fff;
      font-size: 48px;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }

    .name {
      color: #8B959E;
      font-size: 12px;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
      opacity: 0.3;
    }

    .btnGroup {
      padding-top: 15px;

      .sendBtn {
        border: 2px solid #fff;
        border-radius: 50px;
        color: #fff;
        padding: 0px 30px;

        & > span {
          text-transform: capitalize;
          font-family: 'Heebo', sans-serif;
          font-weight: 400;
        }
      }

      .recieveBtn {
        border: 2px solid #B158E2;
        border-radius: 50px;
        color: #fff;
        padding: 0px 10px;
        flex: 1;
        margin-left: 15px;

        & > span {
          text-transform: capitalize;
          font-family: 'Heebo', sans-serif;
          font-weight: 400;
        }
      }
    }

    .adressBlock {
      padding-top: 20px;

      .adress, .qr {
        color: #fff;
        font-size: 14px;
        font-family: 'Heebo', sans-serif;
        font-weight: 400;
      }

      .input {
        flex: 1 1;
        width: 100%;

        .adressInput {
          border: 0;
          background: #F3F4F5;
          border-radius: 5px;
          width: 100%;
          text-indent: 10px;
          margin-top: 15px;
          font-size: 12px;
          font-family: 'Heebo', sans-serif;
          font-weight: 400;

          &:after, &:before {
            display: none;
          }

          input {
            text-align: center;
          }
        }
      }
    }
  }
}
