.tranding {
  width: 25%;

  .selectName {
    border: 0;
    background: #F3F4F5;
    border-radius: 5px;
    width: 130px;
    text-indent: 10px;
    margin-bottom: 15px;

    & > div {
      background: transparent;
    }

    & > div:before, & > div:after {
      border-bottom: 0 !important;
    }
  }


  #select-currency {
    padding: 10px 5px 7px 0;
  }

  .title {
    font-size: 16px;
    color: #fff;
    margin: 0;
    font-family: Heebo, sans-serif;
    font-weight: 400;
  }

  .big {
    font-size: 30px;
    width: 100%;
    font-family: Heebo, sans-serif;
    font-weight: 100;
  }

  .history {
    padding-bottom: 30px;
  }

  .wrapper {
    background: #06062B;
    color: #fff;
    padding: 20px 20px 30px 20px;

    .blockWrapper {
      background-color: #293C8B;
      margin: 30px 0;
      padding: 10px 0;
      border-radius: 3px;

      .count {
        font-size: 18px;
        font-weight: bold;
        padding-right: 10px;
      }

      .currency {
        font-size: 18px;
      }
    }

    .banner {
      padding-bottom: 30px;
    }

    .btnGroup {
      width: 40%;
      cursor: pointer;
      color: #8B959E;

      svg:first-child {
        padding-right: 10px;
      }
    }

    .table {
      padding-top: 30px;

      .row {
        height: 28px;
        font-family: Heebo, sans-serif;
        font-weight: 400;
      }

      .column {
        border-bottom: none;
      }

      .head {
        color: #fff;
        border-bottom: 0;
        padding: 0;
        padding-bottom: 30px;
        font-family: Heebo, sans-serif;
        font-weight: 400;
      }

      .whiteColumn {
        color: #fff;
        padding: 4px 56px 4px 0px;
      }

      .redColumn {
        padding: 0;
        color: #FF005C;
      }

      .greenColumn {
        color: #7AC231;
        padding: 0;
      }

      .greyColumn {
        color: #8B959E;
        padding-right: 0;
      }
    }

    .tableRow {
      height: 0;
    }

  }

  .chartStock {
    position: relative;
    flex: 1;
  }

  .orderBook {
    width: 26%;
  }

  .priceChart {
    width: 46%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .chart {
      max-width: 100%;
      width: 100%;
    }

    .radioBtnGroup {
      justify-content: flex-start;
    }

  }

  .ordersHistory {
    width: 23%;
  }

}

@media (max-width: 1200px) {
  .tranding {
    flex-direction: column;

    .orderBook, .priceChart, .ordersHistory {
      width: 100%;
    }
  }
}


@media (max-width: 1024px) {
  .tranding {
    .big {
      font-size: 30px;
      width: 100%;
      padding: 20px;
    }

    .priceChart .radioBtnGroup {
      display: inline-flex;
    }

    .history {
      padding: 20px;
    }
  }
}

@media (max-width: 767px) {
  .wrapper .react-stockchart svg.react-stockchart {
    right: 30px;
  }
}

@media (max-width: 424px) {
  .chart .radioBtnGroup label {
    width: 51px;
  }

  .chart .radioBtnGroup input[type="radio"] {
    width: 45px;
  }
}
