.NewsScience {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: #06062B !important;
  height: 100%;
  position: relative;
  margin: 0 20px;

  .CardMedia {
    height: 223px;
    width: 100%;

  }

  .Typography {
    width: 70%;
    margin: 20px 0;
    background-color: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-weight: 300;
    font-size: 12px;
    line-height: 158%;
    color: #FFFFFF;
    font-family: Heebo, sans-serif;
    font-weight: 100;
  }


}
