.toPreviousPage {
  padding-bottom: 20px;

  a {
    text-decoration: none;
  }

  .backBtn {
    text-transform: capitalize;
    font-size: 12px;
    padding: 9px 20px;
    background-color: #B158E2;
    font-family: 'Heebo', sans-serif;
    font-weight: 400;

    &:focus, &:hover {
      background-color: #B158E2;
    }


    & > span {
      color: #fff;
    }

    .iconInfo {
      font-size: 10px;
      padding-right: 15px;
    }
  }

  .title {
    color: #fff;
    font-size: 16px;
    padding: 0 40px 0 55px;
    font-family: 'Heebo', sans-serif;
    font-weight: 400;
  }
}

.walletInfo {

  .chartColumn {
    width: 70%;

    .datePicker {
      position: absolute;
      top: 26px;
      padding-right: 35px;
    }

    .chartInfo {
      flex: 1 1;
      position: relative;

      .react-datepicker-popper:last-child {
        margin-left: -70px;

        .react-datepicker__triangle {
          left: 180px !important;
        }
      }

      .chart {
        max-width: 100%;
        width: 100%;

        .title {
          font-size: 30px;
          font-family: 'Heebo', sans-serif;
          font-weight: 400;
        }

        .wrapper {
          width: 100%;
          margin-top: 0;
          padding-bottom: 70px;
        }

        //.lineChart{
        //  width: 97% !important;
        //  height: 100% !important;
        //}

        .recharts-responsive-container {
          width: 100%;
          height: 500px !important;
        }


        .recharts-surface {
          width: 97%;
          height: 100%;
          padding-top: 30px;
        }

        .radioBtnGroup {
          justify-content: flex-start;
          padding-left: 40px;
        }

        .lineChart .yAxis, .lineChart .xAxis {
          font-size: 7px;
        }
      }

      .fromTo {
        color: #8B959E;
        font-size: 12px;
        font-family: 'Heebo', sans-serif;
        font-weight: 400;
        opacity: 0.3;
      }

      .percentage {
        color: #FF005C;
        font-size: 12px;
        background: transparent;
        font-family: 'Heebo', sans-serif;
        font-weight: 400;
      }

      .price {
        color: #fff;
        font-size: 20px;
        font-weight: bold;
      }

      .currency {
        color: #fff;
        font-size: 20px;
        padding-left: 10px;
        font-family: 'Heebo', sans-serif;
        font-weight: 400;
      }

      .data {
        padding: 0 40px 0 30px;
        position: absolute;
        top: 100px;
      }
    }
  }

  .transactionOfWallet {
    width: 30%;
    padding-right: 25px;
    position: relative;

    .transactionWrapper {
      position: relative;
    }


    .transactionWrapper .tabsView header > div > div > div > div {
      justify-content: flex-end;
      padding-right: 20px;
      padding-bottom: 10px;
    }

    .transactionWrapper .tabsView {
      padding-top: 0;
    }

    .title {
      position: absolute;
      top: 3px;
      padding-left: 20px;
      font-family: 'Heebo', sans-serif;
      font-weight: 400;
    }
  }
}

@media screen and(max-width: 1490px) {
  .walletInfo .chartColumn .datePicker {
    position: absolute;
    top: -10px;
  }

  .walletInfo .chartColumn .chartInfo .data {
    padding: 0 40px 0 30px;
    position: absolute;
    top: 85px;
  }
}

@media screen and(max-width: 1126px) {
  .walletInfo .chartColumn {
    width: 65%;
  }

  .walletInfo {
    justify-content: space-between;
  }

  .walletInfo .chartColumn .chartInfo .chart .recharts-responsive-container {
    height: 300px !important;
  }
}

@media screen and(max-width: 1023px) {
  .walletInfo {
    flex-direction: column;

  }

  .walletInfo .transactionOfWallet {
    width: 100%;
    padding-right: 0;
  }

  .walletInfo .chartColumn {
    width: 100%;
  }

  .transactionWrapper {
    margin-bottom: 20px;
  }

  .toPreviousPage {
    justify-content: space-between;
  }

  .walletInfo .chartColumn .chartInfo .data {
    padding: 20px 40px 0 30px;
  }

  .walletInfo .chartColumn .datePicker {
    position: absolute;
    top: 130px;
  }
}

@media screen and(max-width: 767px) {

  .moreVertTab {
    color: #B158E2;
    position: absolute;
    right: 0;
    cursor: pointer;
  }
}

@media screen and(max-width: 500px) {
  .datePicker {
    .period {
      font-size: 11px;
    }

    .pickerInput {
      padding: 5px;
      max-width: 85px;
    }
  }

  .chartInfo {
    .react-datepicker-wrapper:first-of-type {
      padding-right: 10px;
    }
  }
}


@media screen and(max-width: 391px) {
  .chartInfo .react-datepicker-wrapper:first-of-type {
    padding-right: 0px;
  }

  .walletInfo .chartColumn .datePicker {
    padding-right: 20px;
  }

  .walletInfo .chartColumn .chartInfo {
    .price,
    .currency {
      font-size: 18px;
    }

    .data {
      padding: 20px;
    }

    .chart .title {
      font-size: 25px;
    }

    .chart .wrapper .headerContent {
      padding-right: 20px;
      padding-left: 20px;
    }
  }

  .datePicker .pickerInput {
    padding: 5px 2px;
    max-width: 72px;
  }

}
