.flash-wallet-page {
  background: #000116;

  animate-bg {
    background-image: url("../../images/purpur-bg.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
  }

  @media screen and (min-width: 690px) {
    .animate-bg {
      -webkit-animation: bgimagechange 30s infinite; /* Chrome, Safari, Opera */
      animation: 30s infinite bgimagechange;
    }

    @keyframes bgimagechange {
      0% {
        background: url("../../images/purpur-bg.jpg");
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-size: cover;
        background-position: center;
      }
      40% {
        background: url("../../images/purpur-bg.jpg");
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-size: cover;
        background-position: center;
      }
      50% {
        background: url("../../images/grey-bg.jpg");
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-size: cover;
        background-position: center;
      }
      90% {
        background: url("../../images/grey-bg.jpg");
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-size: cover;
        background-position: center;
      }
      100% {
        background: url("../../images/purpur-bg.jpg");
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-size: cover;
        background-position: center;
      }
    }
    /* Chrome, Safari, Opera */
    @-webkit-keyframes bgimagechange {
      0% {
        background: url("../../images/purpur-bg.jpg");
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-size: cover;
        background-position: center;
      }
      40% {
        background: url("../../images/purpur-bg.jpg");
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-size: cover;
        background-position: center;
      }
      50% {
        background: url("../../images/grey-bg.jpg");
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-size: cover;
        background-position: center;
      }
      90% {
        background: url("../../images/grey-bg.jpg");
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-size: cover;
        background-position: center;
      }
      100% {
        background: url("../../images/purpur-bg.jpg");
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-size: cover;
        background-position: center;
      }
    }
  }

  .header {
    position: absolute;
    top: 30px;
    width: 100%;
    z-index: 6;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
  }

  .menu {
    color: #fff;
    font-size: 25px;
    padding-right: 50px;
    display: none;
  }

  .logo-block {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-left: 50px;
    height: 50px;
  }

  .flashlogo {
    padding-bottom: 10px;
  }

  .spacer {
    padding: 0 160px
  }

  .content-wrapper {
    width: 100%;
    height: 100%;
  }

  .title {
    padding-top: 160px;
    width: 50%;
    color: #fff;
    font-size: 49px;
    font-family: 'Heebo', sans-serif;
    font-weight: 300;
  }

  .title .text-bold {
    font-weight: bold;
  }

  .login {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: flex-end;
  }

  .click-block {
    background: #fff;
    color: #000;
    font-family: 'Heebo', sans-serif;
    font-weight: 300;
    font-size: 22px;
    width: 250px;
    height: 105px;
    text-align: center;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    position: relative;
    opacity: 1;
  }

  .click-block:after {
    content: '';
    border: 20px solid transparent;
    border-top: 20px solid #fff;
    position: absolute;
    bottom: -39px;
  }

  .f-circle {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding-top: 30px;
    position: relative;
    border-radius: 50%;
  }

  .f-circle-loader{
    margin-left: 6px;
    position: relative;
    z-index: 4;
  }

  .login-mob:hover > .click-block {
    opacity: 1 !important;
  }

  .login-mob:hover {
    cursor: pointer;
  }

  #loader {
    opacity: 1;
    transition-property: opacity;
    -webkit-transition-property: opacity;
    -moz-transition-property: opacity;

    transition-duration: 2s;
    -webkit-transition-duration: 2s;
    -moz-transition-duration: 2s;

    transition-timing-function: linear;
    -webkit-transition-timing-function: linear;
    -moz-transition-timing-function: linear;

  }

  .loader {
    font-size: 10px;
    position: absolute;
    z-index: 1;
    top: -28px;
    left: 55px;
    margin: 50px auto;
    text-indent: -9999em;
    width: 145px;
    height: 145px;
    border-radius: 50%;
    background: #ffffff;
    background: -moz-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 40%);
    background: -webkit-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 40%);
    background: -o-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 40%);
    background: -ms-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 40%);
    background: linear-gradient(to right, #ffffff 10%, rgba(255, 255, 255, 0) 40%);
    -webkit-animation: load3 7.5s infinite linear;
    -ms-animation: load3 7.5s infinite linear;
    animation: load3 7.5s linear;
    -webkit-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
  }

  .loader:before {
    width: 50%;
    height: 50%;
    background: #ffffff;
    border-radius: 100% 0 0 0;
    position: absolute;
    top: 0;
    left: 0;
    content: '';
  }

  .loader:after {
    width: 75%;
    height: 75%;
    border-radius: 50%;
    content: '';
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
  }

  @-webkit-keyframes load3 {
    0% {
      -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }

  @keyframes load3 {
    0% {
      -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }

  .text {
    font-size: 30px;
    font-family: 'Heebo', sans-serif;
    font-weight: 100;
    text-align: center;
    color: #fff;
    padding-top: 10px;
  }

  .contact-us {
    display: flex;
    flex-direction: column;
    padding-top: 100px;
    padding-bottom: 100px;
  }

  .contact-us .question {
    font-size: 25px;
    font-family: 'Heebo', sans-serif;
    font-weight: 300;
    color: #fff;
  }

  .mob-text {
    display: none;
  }

  .desk-text {
    display: inline-block;
  }

  .start-btn {
    display: none;
  }

  .contact {
    margin-top: 50px;
  }

  .contact-btn {
    padding: 15px 60px;
    font-family: 'Heebo', sans-serif;
    font-size: 25px;
    border: none;
    border-radius: 50px;
    background: linear-gradient(348.69deg, #3795A1 0%, #293C8B 100%);
    box-shadow: 0px 2px 4px rgba(0, 1, 22, 0.5);
    color: #fff;
    font-weight: 300;
    letter-spacing: 0.5px;
    margin-top: 30px;
    margin-left: 40px;
    outline: none;
    cursor: pointer;
  }

  .footer {
    display: none;
  }

  @media screen and (max-width: 1023px) {
    .spacer {
      padding: 0 50px;
    }

    .title {
      font-size: 40px;
    }
  }

  @media screen and (max-width: 690px) {
    .loader {
      left: -2px;
    }

    .title {
      display: none;
    }

    .click-block {
      display: none;
    }

    .login {
      justify-content: center;
    }

    .login-mob {
      display: flex;
      flex-direction: column-reverse;
      align-items: center;
      justify-content: center;
    }

    .spacer {
      padding: 120px 50px 0 50px;
    }

    .start-btn {
      display: flex;
      align-items: center;
      flex-direction: row;
      justify-content: center;
      margin-top: 30px;
    }

    .start {
      padding: 15px 60px;
      font-family: 'Heebo', sans-serif;
      font-size: 25px;
      border: none;
      border-radius: 50px;
      background: #fff;
      box-shadow: 0px 2px 4px rgba(0, 1, 22, 0.5);
      color: #000;
      font-weight: 300;
      letter-spacing: 0.5px;
    }

    .start, .contact-btn {
      padding: 10px 40px;
    }

    .login {
      flex-direction: column;
      align-content: center;
    }

    .mob-text {
      display: inline-block;
      line-height: 1.5;
    }

    .desk-text {
      display: none;
    }

    .question {
      text-align: center;
    }

    .contact {
      display: flex;
      flex-direction: row;
      justify-content: center;
    }

    .contact-btn {
      margin-left: 0;
    }

    .contact-us {
      padding-top: 250px;
    }

    .menu {
      display: inline-block;
    }

    .footer {
      display: flex;
      flex-direction: row;
      align-items: center;
      padding: 0 20px 40px 20px;
    }

    .social-media {
      width: 75%;
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
    }

    .icon {
      color: #000;
    }

    .social {
      width: 30px;
      height: 30px;
      display: flex;
      flex-direction: column;
      background: #fff;
      border-radius: 50%;
      align-items: center;
      justify-content: center;
      margin-left: 15px;
    }

    .logo-footer {
      padding-right: 10px;
      width: 25%;
    }

    .bg {
      background: url("../../images/mob-bg.png") no-repeat center center;
      background-size: contain;
      background-position: top;
      position: relative;
    }

    .login {
      padding-top: 70px;
    }

    .contact-us {
      padding-top: 450px;
    }

    .contact {
      margin-top: 0px;
    }
  }

  @media screen and (max-width: 600px) {
    .contact-us {
      padding-top: 550px;
    }

    .login {
      padding-top: 200px;
    }

    .bg {
      background: url("../../images/mob-bg-2.png") no-repeat center center;
      background-size: contain;
      background-position: top;
      position: relative;
    }
  }

  @media screen and (max-width: 545px) {
    .contact-us {
      padding-top: 450px;
    }
  }

  @media screen and (max-width: 495px) {

    .login {
      padding-top: 100px;
    }
  }

  @media screen and (max-width: 425px) {
    .social-media {
      justify-content: space-between;
    }

    .social {
      margin-left: 0px;
    }

    .text {
      font-size: 25px;
    }

    .question {
      font-size: 22px;
    }

    .spacer {
      padding: 120px 15px 0 15px;
    }

    .menu {
      padding-right: 25px;
    }

    .logo-block {
      padding-left: 25px;
    }

    .bg {
      background: url("../../images/bg-mob.png") no-repeat center center;
      background-size: contain;
      background-position: top;
      position: relative;
    }

    .login {
      padding-top: 30px;
    }

  }

  @media screen and (max-width: 375px) {
    .contact-us {
      padding-top: 250px;
    }
  }

  @media screen and (max-width: 350px) {
    .contact-us {
      padding-top: 250px;
    }

    .login {
      padding-top: 0px;
    }
  }

}
