.transfert{
    .content{
        width: 70%;
        margin: 0 auto;

        .subtitle{
            color: #fff;
            padding: 0;
            margin: 10px 0;
            font-weight: 400;
        }

        .copyInput{
            position: relative;

            .copy-icon{
                width: 20px;
                height: auto;
                position: absolute;
                right: 20px;
                bottom: 7px;
            }

            input{
                padding: 11px 44px 11px 11px;
            }
        }

        .selectandinput{
            & > div:last-child{
                margin-left: 10px;
            }

            .selectCurrency > div > div{
                padding: 11px 8px 0px 8px;
            }
        }

        .message-block{
            background: #293C8B;
            padding: 10px 20px;
            margin-bottom: 20px;

            p{
                color: #fff;
                font-size: 12px;
                line-height: 18px;
            }
        }

        .continueBtn{
            text-transform: capitalize;
            font-size: 16px;
            color: #fff;
            padding: 9px 20px;
            background-color: #B158E2;
            font-weight: 400;
            width: 100%;
            margin-top: 30px;

            &:hover {
                background-color: #B158E2;
            }
        }


        .label {
            text-transform: uppercase;
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 18px;
            color: #FFFFFF;
            margin: 11px 0;
        }
    }
}