.transactionsComponent {
  height: calc(100% - 105px);

  & > div > div:first-child {
    margin-right: 20px;
  }

  .transactionBlock {
    flex: 1;
  }
}

.bigTransactions {
  .address {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .firstRow {
    .dateBlock {
      max-width: 400px;
    }
  }
}


@media screen and(max-width: 1023px) {
  .transactionsComponent > div > div:first-child {
    margin-right: 0;
  }

  .transaction {

    .date {
      width: 120px;
    }


    .bigTransactions {
      flex-direction: column;

      .firstRow {
        flex-direction: column;

        .address {
          padding: 30px 0;
        }

        .dateBlock {
          max-width: 100%;
        }
      }

      & > div > div {
        width: 100%;
        justify-content: space-between;
      }
    }

    .priceData {
      justify-content: space-between;
      max-width: 100%;
      width: 100%;
    }
  }

  .transactionBigWrapper .tabsView .information {
    width: 100%;
    justify-content: space-between;
  }
}
